.Logo {
    background-color: transparent;
    height: 100%;
    box-sizing: border-box;
    border-radius: 5px;
    margin-right: 10px;
    margin-left: 100px;
}

.Logo img{
    height: 50px;
}
@media(max-width:499px){
    .Logo{
        margin-right: 5px;   
        margin-left: 0px;
    }
}